


















































import {Component, Prop, Vue} from "vue-property-decorator";
import Chip from "@/components/chip.vue";
import {ShippingModel} from "@/models/shipping";

@Component({
  components:{
    Chip
  }
})export default class ItemShipping extends Vue {
  @Prop({type: Object, required: true})  readonly shipping!: ShippingModel
  @Prop({type: Number, required: true})  readonly count!: number



  textTypeService(type: number){
    switch (type) {
      case 0:
        return "Inicial"
      case 1:
        return "Entrega"
      case 2:
        return "Devolucion"
    }
  }

  textStatus(status: number){
    switch (status) {
      case 0:
      case 7:
        return 'Cancelado'
      case 1:
        return 'En proceso'
      case 2:
        return "Recogido"
      case 3:
        return "En camino"
      case 4:
        return "Entregado"
      case 6:
        return "Devolución"
      case 8:
        return "Esperando otro repartidor"

    }
  }
}
