




























































































































































































































































import Chip from "@/components/chip.vue";

import {Component, Vue} from "vue-property-decorator";
import ItemShipping from "@/components/couriers/ItemShipping.vue";

export interface shippingT{
  id: string
  reserve_date: string
  status: number,
  type_delivery: number,
  total: number,
  payment_location: true
  distance: number
  duration: number
}

@Component({
  components:{
    Chip,
    ItemShipping
  }
})
export default class ActiveCourierMap extends Vue{

  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/dboys/';


  getTotal(shippings:shippingT[]): number {
    let total = 0;
    shippings.forEach(value => {
      if(value.type_delivery == 0){
        total += value.total;
      }else{
        if(value.payment_location){
          total += value.total;
        }
      }
    });
    console.log(total)
    return total;


  }


  getDistance(shippings:shippingT[]): number {
    let distance = 0;
    shippings.forEach(value => {
      if(value.type_delivery == 0){
        distance += value.distance;
      }else{
        if(value.payment_location){
          distance += value.distance;
        }
      }
    });
    return distance;


  }

  getHours(shippings:shippingT[]): number {
    let duration = 0;
    shippings.forEach(value => {
      if(value.type_delivery == 0){
        duration += value.duration;
      }else{
        if(value.payment_location){
          duration += value.duration;
        }
      }
    });
    return duration /  3600;
  }


  created(){

  }
}
